<template>
    <div class="home">
        <div class="header">
            <div class="address_map" @click="$router.push({name: 'address',params: {city: city}})">
                <i class="fa fa-map-marker"></i>
                <span>{{address}}</span>
                <i class="fa fa-sort-desc"></i>
            </div>
        </div>
        <div class="search_wrap" :class="{'fixedview':showFilter}" @click="$router.push('/search')">
            <div class="shop_search">
                <i class="fa fa-search"></i>
                搜索商家 商家名称
            </div>
        </div>
        <div id="containner">
            <!-- 轮播 -->
            <mt-swipe :auto="4000" class="swiper">
                <mt-swipe-item v-for="(img,index) in swipeImgs" :key="index">
                    <img :src="img" alt="">
                </mt-swipe-item>
            </mt-swipe>
            <!-- 美食分类 -->
            <mt-swipe :auto="0" class="entries">
                <mt-swipe-item v-for="(entry,i) in entries" :key="i" class="entry_wrap">
                    <div class="foodentry" v-for="(item,index) in entry" :key="index">
                        <div class="img_wrap">
                            <img :src="item.image" alt>
                        </div>
                        <span>{{item.name}}</span>
                    </div>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <!-- 推荐商家 -->
        <div class="shoplist-title">推荐商家</div>
        <!-- 导航栏 -->
        <FilterView :filterData="filterData" @searchFixed="showFilterView" @update="update"></FilterView>
        <!-- 商家信息 -->
        <mt-loadmore :top-method="loadData" :bottom-method="loadMore" :bottom-all-loaded="allLoaded" ref="loadmore"
            :autoFill="false" :bottomPullText="bottomPullText">
            <div class="shoplist">
                <IndexShop v-for="(item,index) in restaurants" :key="index" :restaurant="item.restaurant"></IndexShop>
            </div>
        </mt-loadmore>

    </div>
</template>
<script>
    import FilterView from '../components/FilterView'
    import IndexShop from '../components/IndexShop'


    //轮播    
    import { Swipe, SwipeItem, Loadmore } from 'mint-ui';
    export default {
        name: 'Home',
        data() {
            return {
                swipeImgs: [],
                entries: [],
                //导航信息
                filterData: null,
                //导航栏是否固定
                showFilter: false,
                //商家信息
                page: 1,
                size: 5,
                restaurants: [],
                // 若数据已全部获取完毕
                allLoaded: false,
                //上拉加载更多 文本
                bottomPullText: '上拉加载更多',
                //向服务器传的数据（排序的数据）
                data:null,


            }
        },
        components: {
            FilterView,
            IndexShop,
            
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                //导航栏综合排序
                this.$axios.get('/api/profile/shopping')
                    .then(res => {
                        // console.log(res.data);
                        this.swipeImgs = res.data.swipeImgs;
                        this.entries = res.data.entries;
                    });
                //导航栏筛选
                this.$axios.get('/api/profile/filter')
                    .then(res => {
                        // console.log(res.data);
                        this.filterData = res.data;

                    });
                //获取商家信息
                this.loadData();
            },
            //控制导航栏是否固定
            showFilterView(isShow) {
                this.showFilter = isShow;
            },
            
            //下拉刷新
            loadData() {
                this.page = 1;
                this.allLoaded = false;
                this.bottomPullText = "上拉加载更多";
                // 商家信息
                this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.data)
                    .then(res => {
                        console.log(res);
                        this.$refs.loadmore.onTopLoaded();
                        this.restaurants = res.data;
                    })
            },
            //上拉加载更多
            loadMore() {
                if (!this.allLoaded) {//如果数据还有
                    this.page++;
                    this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`)
                        .then(res => {
                            this.$refs.loadmore.onBottomLoaded();
                            if (res.data.length > 0) {
                                res.data.forEach(item=>{
                                    this.restaurants.push(item);
                                });
                                if(res.data<this.size){//如果加载的条数小于5那么没有更多数据了 
                                    this.allLoaded = true;// 若数据已全部获取完毕
                                    this.bottomPullText = "没有更多了哦"; 
                                }
                            } else {//数据没有了  
                                this.allLoaded = true;// 若数据已全部获取完毕
                                this.bottomPullText = "没有更多了哦";

                            }

                        })
                }
            },
            //更新数据
            update(condation) {
                // console.log(condation)
                this.data = condation;
                this.loadData();
            },

        },
        computed: {
            address() {
                return this.$store.getters.address;
            },
            city() {
                return (
                    this.$store.getters.location.addressComponent.city ||
                    this.$store.getters.location.addressComponent.province
                );
            }
        }
    }
</script>
<style scoped>
    .home {
        width: 100%;
        height: 100%;
        overflow: auto;
        box-sizing: border-box;
    }

    .header,
    .search_wrap {
        background-color: #009eef;
        padding: 10px 16px;
    }

    .header .address_map {
        color: #fff;
        font-weight: bold;
    }

    .address_map i {
        margin: 0 3px;
        font-size: 18px;
    }

    .address_map span {
        display: inline-block;
        width: 80%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .search_wrap .shop_search {
        /* margin-top: 10px; */
        background-color: #fff;
        padding: 10px 0;
        border-radius: 4px;
        text-align: center;
        color: #aaa;
    }

    .search_wrap {
        position: sticky;
        top: 0px;
        z-index: 999;
        box-sizing: border-box;
    }

    .swiper {
        height: 100px;
    }

    .swiper img {
        width: 100%;
        height: 100px;
    }

    .entries {
        background: #fff;
        height: 47.2vw;
        text-align: center;
        overflow: hidden;
    }

    .foodentry {
        width: 20%;
        float: left;
        position: relative;
        margin-top: 2.933333vw;
    }

    .foodentry .img_wrap {
        position: relative;
        display: inline-block;
        width: 12vw;
        height: 12vw;
    }

    .img_wrap img {
        width: 100%;
        height: 100%;
    }

    .foodentry span {
        display: block;
        color: #666;
        font-size: 0.32rem;
    }

    /* 推荐商家 */
    .shoplist-title {
        display: flex;
        align-items: flex;
        justify-content: center;
        height: 9.6vw;
        line-height: 9.6vw;
        font-size: 16px;
        color: #333;
        background: #fff;
    }

    .shoplist-title:after,
    .shoplist-title:before {
        display: block;
        content: "一";
        width: 5.333333vw;
        height: 0.266667vw;
        color: #999;
    }

    .shoplist-title:before {
        margin-right: 3.466667vw;
    }

    .shoplist-title:after {
        margin-left: 3.466667vw;
    }

    .fixedview {
        width: 100%;
        position: fixed;
        top: 0px;
        z-index: 999;
    }

    .mint-loadmore {
        height: calc(100% - 95px);
        overflow: auto;
    }
</style>